'use client';
import { useState } from 'react';
import { FaEnvelope, FaLock, FaSpinner } from 'react-icons/fa';
import Notification from './Notification'; // Import the Notification component

export default function LoginForm() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [loading, setLoading] = useState(false);
    const [notification, setNotification] = useState({ message: '', type: '', visible: false });

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setSuccess('');
        setLoading(true);

        try {
            const response = await fetch('/api/login', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ email, password }),
            });

            const data = await response.json();

            if (!response.ok) {
                throw new Error(data.message || 'Something went wrong');
            }
            setSuccess(data.message);
            setNotification({ message: data.message, type: 'success', visible: true });
        } catch (err) {
            setError(err.message);
            setNotification({ message: err.message, type: 'error', visible: true });
        } finally {
            setLoading(false);
        }
    };

    const closeNotification = () => {
        setNotification({ ...notification, visible: false });
    };

    return (
        <div className="flex items-center justify-center min-h-screen bg-gray-100">
            <div className="bg-white p-8 rounded-lg shadow-xl w-96">
                <div className="text-center mb-6">
                    <h1 className="text-3xl font-bold text-gray-800 mt-2">OLO SHOP</h1>
                    <p className="text-gray-500 mt-2">"Shop smart, live better."</p>
                </div>

                <form onSubmit={handleSubmit}>
                    {/* Email Input */}
                    <div className="mb-4 relative">
                        <label className="block text-gray-700 font-medium mb-2" htmlFor="email">Email</label>
                        <div className="flex items-center bg-gray-50 p-2 rounded-lg border border-gray-300 focus-within:border-blue-500 transition">
                            <FaEnvelope className="text-gray-500 mr-2" />
                            <input
                                className="w-full bg-transparent outline-none text-gray-700"
                                type="email"
                                id="email"
                                placeholder="Enter your email"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                required
                            />
                        </div>
                    </div>

                    {/* Password Input */}
                    <div className="mb-6 relative">
                        <label className="block text-gray-700 font-medium mb-2" htmlFor="password">Password</label>
                        <div className="flex items-center bg-gray-50 p-2 rounded-lg border border-gray-300 focus-within:border-blue-500 transition">
                            <FaLock className="text-gray-500 mr-2" />
                            <input
                                className="w-full bg-transparent outline-none text-gray-700"
                                type="password"
                                id="password"
                                placeholder="Enter your password"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required
                            />
                        </div>
                    </div>

                    {/* Loading Animation */}
                    {loading && (
                        <div className="flex justify-center mb-4">
                            <FaSpinner className="animate-spin text-blue-500 text-2xl" />
                        </div>
                    )}

                    {/* Login Button */}
                    <button
                        className={`w-full bg-blue-600 text-white p-2 rounded-lg hover:bg-blue-700 transition duration-300 focus:outline-none flex justify-center items-center ${loading ? 'opacity-50' : ''}`}
                        type="submit"
                        disabled={loading}
                    >
                        {loading ? (
                            <FaSpinner className="animate-spin text-white" />
                        ) : (
                            'Login'
                        )}
                    </button>
                </form>
            </div>

            {/* Notification Component */}
            {notification.visible && (
                <Notification
                    message={notification.message}
                    type={notification.type === 'success' ? 'success' : 'error'}
                    onClose={closeNotification}
                />
            )}
        </div>
    );
}